"use client";
import React from "react";
import { FaPlaneDeparture, FaPlane, FaPassport, FaHeart, FaChevronLeft, FaChevronRight } from "react-icons/fa";
import { MdOutlineKingBed } from "react-icons/md";

export default function TopDestinations({
  trips = [],
  title = "Top Packages",
  subtitle = "Handpicked Getaways for Every Traveler",
}) {
  return (
    <section className="max-w-7xl mx-auto py-16 px-4">
      {/* Heading and subtitle */}
      <div className="flex justify-between items-center mb-8">
        <div>
          <h2 className="text-3xl font-bold">{title}</h2>
          <p className="text-gray-700">{subtitle}</p>
        </div>
        <div className="flex items-center gap-x-3">
          <button
            className="bg-black text-white p-3 rounded-full shadow-md hover:bg-gray-700 transition-all"
            type="button"
          >
            <FaChevronLeft />
          </button>
          <button
            className="bg-black text-white p-3 rounded-full shadow-md hover:bg-gray-700 transition-all"
            type="button"
          >
            <FaChevronRight />
          </button>
        </div>
      </div>

      {/* Packages grid */}
      {trips.length === 0 ? (
        <div className="text-center py-10">
          <p className="text-xl text-gray-600">No packages found for your selection.</p>
        </div>
      ) : (
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-8">
          {trips.map((trip) => (
            <div
              key={trip._id}
              className="bg-white border border-gray-200 rounded-2xl overflow-hidden shadow hover:shadow-lg transition flex flex-col"
            >
              <div className="relative">
                <img
                  src={trip.images[0]?.url || "https://images.pexels.com/photos/3601420/pexels-photo-3601420.jpeg"}
                  alt={trip.title}
                  className="w-full h-48 object-cover"
                />
                {/* INR price badge in top right */}
                <span className="absolute top-3 right-3 bg-[#FF914D] text-white text-xs font-semibold px-3 py-1 rounded-full shadow">
                  Starts: INR {trip.pricing?.valuePack || "Contact"}
                </span>
              </div>
              <div className="p-5 flex-1 flex flex-col">
                {/* Promo image above title */}
                <div className="flex justify-center mb-4">
                  <img
                    src="/images/single-promo.jpg"
                    alt="Promo"
                    className="w-16 h-16 object-cover rounded-lg"
                  />
                </div>
                {/* Title row: title and heart */}
                <div className="flex items-center justify-between mb-2">
                  <h3 className="text-lg font-semibold flex-1">{trip.title}</h3>
                  <span className="ml-2 text-[#FF507A]">
                    <FaHeart />
                  </span>
                </div>
                <div className="flex items-center gap-4 text-sm text-gray-600 mb-2">
                  <span className="flex items-center gap-1">
                    <FaPlaneDeparture className="text-[#FF507A]" />
                    {trip.destinationType}
                  </span>
                  <span className="flex items-center gap-1">
                    <MdOutlineKingBed className="text-[#FF507A]" />
                    {trip.destinationCountry}
                  </span>
                </div>
                <div className="flex items-center gap-4 text-xs text-gray-500 mb-2">
                  <span className="flex items-center gap-1">
                    <FaPlane className="text-[#FF507A]" />
                    <img
                      src="/images/air-india-logo.png"
                      alt="Delhi airport"
                      className="w-5 h-5 object-contain ml-1"
                    />
                  </span>
                  <span className="h-5 border-l border-gray-300 mx-2"></span>
                  <span className="flex items-center gap-1">
                    <FaPassport className="text-[#FF507A]" />
                    <img
                      src="/images/india-flag.png"
                      alt="Visa"
                      className="w-5 h-5 object-contain ml-1"
                    />
                  </span>
                </div>
                <div className="w-full border-t border-gray-300 my-2"></div>
                <div className="text-xs text-gray-500 mb-4">
                  INCLUDES: Accommodation, Speed boat Transfer, Green Tax, 24x7 online support
                </div>
                <button className="mt-auto bg-gradient-to-r from-[#FF3131] to-[#FF914D] text-white text-sm font-semibold px-4 py-2 rounded-lg">
                  View Details
                </button>
              </div>
            </div>
          ))}
        </div>
      )}

      {/* Extra content below Top Packages */}
      <div className="mt-16">
        <h2 className="text-2xl font-bold mb-4">Why Choose Our Packages?</h2>
        <p className="text-gray-700 mb-2">
          Our packages are handpicked for every traveler, ensuring the best experience, comfort, and value for money.
        </p>
        <ul className="list-disc pl-6 text-gray-600">
          <li>Expert curated itineraries</li>
          <li>24x7 support</li>
          <li>Best price guarantee</li>
          <li>Flexible booking options</li>
        </ul>
      </div>
    </section>
  );
}